<template>
  <div>
    <h1>Yeni Bir İsim Ekleyin!</h1>
    <div class="hepsi-div">
      <form autocomplete="off" action="#" ref="myForm">
        <table>
          <thead>
            <tr>
              <td>İsim</td>
              <td>Grup</td>
            </tr>
            <tr class="spacer"></tr>
          </thead>
          <tbody>
            <Kisi
              @kisiDeleted="getAllOfEm()"
              v-for="kişi in all"
              v-bind:key="kişi._id"
              :kisi="kişi"
            ></Kisi>
            <tr id="newTr" ref="newTr">
              <td>
                <input
                  autocomplete="off"
                  tabindex="1"
                  ref="myInput"
                  id="name"
                  placeholder="İsim"
                  v-model="newName"
                  type="text"
                />
              </td>
              <td style="text-align:center">
                <select
                  autocomplete="off"
                  name="grup"
                  id="grup"
                  v-model="group"
                  tabindex="2"
                  ><option value="A">A</option
                  ><option value="B">B</option></select
                >
              </td>
              <td>
                <label
                  ><input
                    @click.prevent="ekle()"
                    style="display:none;"
                    type="submit"/>
                  <svg
                    tabindex="3"
                    @keyup.enter="ekle()"
                    ref="addBtn"
                    @click.prevent="ekle()"
                    height="50"
                    width="50"
                  >
                    <circle
                      cx="25"
                      cy="25"
                      r="23"
                      stroke="#22bf2d"
                      stroke-width="3"
                      fill="#92ff9d"
                    />
                    <path
                      d="M15 25 L35 25 Z"
                      stroke="#22bf2d"
                      stroke-width="2"
                    />
                    <path
                      d="M25 15 L25 35 Z"
                      stroke="#22bf2d"
                      stroke-width=""
                    /></svg
                ></label>
              </td>
            </tr>

            <tr ref="spaceBeforeYeniEkle" class="spacer"></tr>
            <tr ref="yeniEkle">
              <td style="text-align:center">
                <span
                  class="new-button"
                  @click="newTrGoster()"
                  tabindex="4"
                  @keyup.enter="newTrGoster()"
                  >Yeni Ekle</span
                >
              </td>
            </tr>
          </tbody>
        </table>
      </form>
    </div>
  </div>
</template>

<script>
import EventService from '@/services/EventService'
import Kisi from '@/components/Kişi'
export default {
  components: {
    Kisi
  },
  data() {
    return {
      newName: '',
      group: '',
      all: []
    }
  },
  methods: {
    ekle() {
      EventService.createName({ name: this.newName, group: this.group })
        .then(res => {
          console.log(res.data)
          this.$refs.myInput.setAttribute(
            'placeholder',
            'Başarıyla kaydedildi!'
          )
          this.getAllOfEm()
          this.$refs.myInput.focus()
          this.newName = ''
        })
        .catch(err => {
          console.log(err)
        })
    },
    getAllOfEm() {
      EventService.getAll()
        .then(res => {
          this.all = res.data
        })
        .catch(err => {
          console.log(err)
        })
    },
    newTrGoster() {
      this.$refs.newTr.style.display = 'table-row'
      this.$refs.myInput.focus()
      this.$refs.spaceBeforeYeniEkle.style.display = 'none'
      this.$refs.yeniEkle.style.display = 'none'
    }
  },
  created() {
    this.getAllOfEm()
  },
  mounted() {
    var myForm = this.$refs.myForm
    myForm.addEventListener('focusouted', () => {
      console.log('focusouted')
      this.$refs.newTr.style.display = 'none'
    })
  }
}
</script>

<style scoped>
h1 {
  margin-top: 0;
}
input {
  font-size: 1em;
  display: inline-block;
  margin: auto;
  border: none;
  width: 230px;
  padding: 10px 20px;
  border-radius: 999px;
}
select {
  font-size: 1em;
  padding: 10px;
  border: none;
  border-radius: 999px;
}
.hepsi-div {
  width: fit-content;
  margin: 20px auto;
  border: 2px solid #96897b;
  padding: 20px;
  border-radius: 10px;
  background-color: rgb(211, 196, 179);
  height: fit-content;
}
table {
  text-align: left;
}
thead td {
  font-weight: 600;
  padding: 10px;
  border-bottom: 3px solid #96897b;
  border-radius: 10px;
}
.spacer {
  height: 10px;
}
.new-button {
  padding: 10px;
  color: rgb(136, 175, 131);
  background-color: #c2eabd;
  border: 2px solid rgb(136, 175, 131);
  border-radius: 999px;
  cursor: pointer;
}
#newTr {
  display: none;
}
</style>
