<template>
  <div id="top">
    <h2 class="header">
      <i
        >"..ben sayıyorum. ooğoğoğo portakalı soyuyore, baş ucuma koyuyore.."</i
      >
    </h2>
    <div class="goster">
      <div class="groups">
        <group :array="groupA" groupName="A" />
        <group :array="groupB" groupName="B" />
      </div>
      <div class="btnDiv">
        <span class="mixBtn" @click="mix()">Grupları Komple Karıştır</span>
      </div>
      <div class="btnDiv">
        <span class="mixBtn" @click="mixSabit()"
          >Gruplar Aynı Kalsın, İsimleri Karıştır</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import Group from '@/components/Group'

import EventService from '@/services/EventService'

export default {
  components: {
    Group
  },
  data() {
    return {
      groupA: [],
      groupB: [],
      all: []
    }
  },
  created() {
    EventService.getA()
      .then(res => {
        this.groupA = res.data
        this.groupA.sort(() => Math.random() - 0.5)
      })
      .catch(error => {
        console.log(error)
      })
    EventService.getB()
      .then(res => {
        this.groupB = res.data
        this.groupB.sort(() => Math.random() - 0.5)
      })
      .catch(error => {
        console.log(error)
      })
  },
  methods: {
    mix() {
      EventService.getAll()
        .then(res => {
          this.all = res.data
          this.all.sort(() => Math.random() - 0.5)
          this.groupA = this.all.slice(0, Math.floor(this.all.length / 2))
          this.groupB = this.all.slice(
            Math.floor(this.all.length / 2),
            this.all.length
          )
          console.log(this.groupA)
          console.log(this.groupB)
        })
        .catch(err => {
          console.log(err)
        })
    },
    mixSabit() {
      this.groupA.sort(() => Math.random() - 0.5)
      this.groupB.sort(() => Math.random() - 0.5)
    }
  }
}
</script>

<style scoped>
.goster {
  padding: 20px;
  border: 2px solid #628395;
  background-color: #c1e2f7;
  border-radius: 20px;
}
.groups {
  display: flex;
}
.header {
  margin: 0;
  margin-bottom: 20px;
}
div {
  width: fit-content;
  margin: auto;
}
.mixBtn {
  padding: 20px;
  border: 1px solid gray;
  border-radius: 999px;
  cursor: pointer;
  display: block;
}
.mixBtn:hover {
  background-color: rgba(0, 0, 0, 0.137);
}
.btnDiv {
  margin: 10px auto;
  width: fit-content;
}
</style>
