import axios from 'axios'
const NETWORK =
  window.location.origin == 'http://localhost:8080'
    ? 'http://localhost:3000'
    : window.location.origin

const apiClient = axios.create({
  baseURL: NETWORK,
  withCredentials: false,
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json'
  }
})

export default {
  getAll() {
    return apiClient.get('/names')
  },
  getA() {
    return apiClient.get('/names/A')
  },
  getB() {
    return apiClient.get('/names/B')
  },
  getName(id) {
    return apiClient.get('/name/' + id)
  },
  createName(obj) {
    return apiClient.post('/names', obj)
  },
  deleteById(id) {
    return apiClient.post('/name/delete/' + id)
  }
}
