<template>
  <div class="group-div">
    <h3>{{ groupName }} grubundan olanlar çıkıyore</h3>
    <div class="isimler">
      <ol>
        <li class="kişi-li" v-for="kişi in göster" :key="kişi._id">
          {{ kişi.name }}
        </li>
      </ol>
    </div>
    <button class="seç" @click="seç()">Sıradakini Seç</button>
  </div>
</template>

<script>
export default {
  name: 'Group',
  data() {
    return {
      göster: []
    }
  },
  props: {
    array: {
      type: Array,
      default() {
        return []
      }
    },
    groupName: {
      type: String,
      default: 'groupName'
    }
  },
  created() {
    this.array.sort(() => Math.random() - 0.5)
  },
  methods: {
    seç() {
      this.göster = this.array.slice(0, this.göster.length + 1)
    }
  },
  watch: {
    array() {
      this.göster = []
    }
  }
}
</script>

<style scoped>
.group-div {
  width: fit-content;
  margin: 20px;
  border: 2px solid #ffd588;
  border-radius: 10px;
  background-color: #fdf5e5;
  height: fit-content;
}

.isimler {
  padding: 15px;
}
h3 {
  margin: 0 auto;
  border-bottom: 2px solid #ffd588;
  border-radius: 0 0 10px 10px;
  padding: 20px;
}
.kişi-li {
  font-size: 1em;
  text-align: left;
  padding-left: 20px;
}

.seç {
  display: block;
  width: 100%;
  padding: 15px;
  margin: 0;
  border: none;
  border-radius: 0 0 10px 10px;
  cursor: pointer;
  background-color: #ff6978;
}
.seç:hover {
  background-color: #c71829;
  color: white;
}
</style>
