<template>
  <tr>
    <td>
      {{ kisi.name }}
    </td>
    <td>
      {{ kisi.group }}
    </td>
    <td>
      <svg @click="deleteKişi()" height="50" width="50">
        <circle
          cx="25"
          cy="25"
          r="23"
          stroke="#FF6978"
          stroke-width="3"
          fill="#ffbec4"
        />
        <path d="M20 25 L30 25 Z" stroke="red" stroke-width="2" />
      </svg>
    </td>
  </tr>
</template>

<script>
import EventService from '@/services/EventService'
export default {
  props: {
    kisi: {
      type: Object
    }
  },
  methods: {
    deleteKişi() {
      EventService.deleteById(this.kisi._id)
        .then(res => {
          console.log(res.data)
          this.$emit('kisiDeleted')
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style scoped>
tbody tr td:nth-of-type(2) {
  text-align: center;
}
.delete {
  border-radius: 9999px;
}
</style>
